<template>
  <div>
    <vue-html2pdf
      :show-layout="false"
      :float-layout="true"
      :enable-download="false"
      :preview-modal="true"
      :paginate-elements-by-height="1400"
      :filename="`Registro-${item_selected.dni}`"
      :pdf-quality="2"
      :manual-pagination="false"
      pdf-format="a4"
      pdf-orientation="portrait"
      pdf-content-width="800px"
      ref="html2Pdf"
      class=" h-full m-0 p-0"
      @change="close"
    >
      <section
        slot="pdf-content"
        class="m-0 p-14 text-black text-sm font-medium"
      >
        <!-- PDF Content Here -->

        <section class=" flex justify-between items-center m-0">
          <div class=" w-24">
            <img src="@//assets//images//unap.jpg" alt="" />
          </div>

          <div class=" w-48 text-right uppercase">
            <span>dirección de admisión</span>
          </div>
        </section>

        <section class="mt-3">
          <div class=" text-center">
            <h1 class=" text-4xl font-bold">{{ item_selected.admision }}</h1>
            <h2 class=" text-xl">{{ item_selected.modalidad }}</h2>
            <h3 class=" text-lg">{{ item_selected.modalidad_referencia }}</h3>
          </div>
        </section>

        <section class=" mt-3 flex items-center justify-center text-lg">
          <div class=" border border-black w-3/5">
            <div class=" flex p-2">
              <div class=" w-1/2">
                <span>Expendiende N°</span>
              </div>

              <div>
                :
              </div>

              <div
                class=" w-1/2 flex items-center justify-center text-center font-bold"
              >
                <span>{{ completar00(item_selected.id) }}</span>
              </div>
            </div>

            <div class=" flex p-2">
              <div class=" w-1/2">
                <span>Código Postulante</span>
              </div>

              <div>
                :
              </div>

              <div
                class=" w-1/2 flex items-center justify-center text-center font-bold"
              >
                <span>{{ item_selected.dni }}</span>
              </div>
            </div>
          </div>
        </section>

        <section class=" mt-5">
          <div class=" flex">
            <div class=" w-1/5">
              Nombres
            </div>
            <div>:</div>
            <div class=" w-4/5 border-black border-b text-center uppercase">
              <span>{{ item_selected.nombre }}</span>
            </div>
          </div>

          <div class=" flex mt-3">
            <div class=" w-1/5">
              Apellidos
            </div>
            <div>:</div>
            <div class=" w-4/5 border-black border-b text-center uppercase">
              <span>{{
                `${item_selected.apellido_paterno} ${item_selected.apellido_materno}`
              }}</span>
            </div>
          </div>

          <div class=" flex mt-3">
            <div class=" w-1/5">
              Dni
            </div>
            <div>:</div>
            <div class=" w-4/5 border-black border-b text-center">
              <span>{{ item_selected.dni }}</span>
            </div>
          </div>

          <div class=" flex mt-3">
            <div class=" w-1/5">
              Domicilio
            </div>
            <div>:</div>
            <div class=" w-4/5 border-black border-b uppercase text-center">
              <span>{{ item_selected.direccion }}</span>
            </div>
          </div>

          <div class=" flex mt-3">
            <div class=" w-1/5">
              Distrito
            </div>
            <div>:</div>
            <div class=" w-4/5 border-black border-b text-center uppercase">
              <span>{{ item_selected.distrito }}</span>
            </div>
          </div>

          <div class=" flex mt-3">
            <div class=" w-1/5">
              Telefono Fijo
            </div>
            <div>:</div>
            <div class=" w-2/5 border-black border-b text-center">
              <span>{{ item_selected.telefono }}</span>
            </div>
          </div>

          <div class=" flex mt-3">
            <div class=" w-1/5">
              Celular
            </div>
            <div>:</div>
            <div class=" w-2/5 border-black border-b text-center">
              <span>{{ item_selected.celular }}</span>
            </div>
          </div>
        </section>

        <section class=" mt-10">
          <div class=" text-center font-semibold text-lg">
            <span>Facultad a la que postula</span>
          </div>
        </section>

        <section class=" mt-10">
          <div class=" flex">
            <div class=" w-3/12">
              Programa de Estudio
            </div>
            <div>:</div>
            <div class=" w-5/12 border-black border-b text-center uppercase">
              <span>{{ item_selected.escuela }}</span>
            </div>

            <div>Código:</div>
            <div class=" w-4/12 border-black border-b text-center uppercase">
              <span>{{ item_selected.codigo_escuela }}</span>
            </div>
          </div>

          <div class=" flex mt-3">
            <div class=" w-4/12">
              Modalidad que postula
            </div>
            <div>:</div>
            <div class=" w-4/12 border-black border-b text-center uppercase">
              <span>{{ item_selected.modalidad }}</span>
            </div>

            <div>Código:</div>
            <div class=" w-4/12 border-black border-b text-center uppercase">
              <span>{{ item_selected.codigo_modalidad }}</span>
            </div>
          </div>
        </section>

        <section class=" mt-10 flex text-center justify-center">
          <div class=" border border-black w-3/5">
            <div>
              Registrador
            </div>

            <div class="border-black border-b">
              <span></span>
            </div>

            <div class=" flex p-2">
              <div>Firma:</div>
            </div>

            <div class="border-black border-b p-5"></div>

            <div class=" flex p-2">
              <div>Nombre:</div>

              <div class=" text-center w-full font-medium uppercase">
                <span>{{ get_auth.first_name }}</span>
              </div>
            </div>
          </div>
        </section>
      </section>
    </vue-html2pdf>
  </div>
</template>

<script>
// import VueHtml2pdf from "vue-html2pdf";

const VueHtml2pdf = () => import("vue-html2pdf");

export default {
  props: {
    item_selected: {
      type: Object,
    },
  },

  components: {
    VueHtml2pdf,
  },

  computed: {
    get_auth() {
      return this.$store.getters["auth/get_auth"];
    },
  },

  mounted() {
    setTimeout(() => {
      this.generateReport();
    }, 500);
  },

  destroyed() {
    this.$emit("hasgenerate");
  },

  methods: {
    close() {
      this.$emit("hasgenerate");
    },

    generateReport() {
      this.$refs.html2Pdf.generatePdf();
    },

    completar00(val) {
      if (val.toString().length < 10) {
        let final = 10 - val.toString().length;
        let result = "";
        for (let index = 0; index < final; index++) {
          result += "0";
        }
        result += val.toString();
        return result;
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
