<template>
  <div>
    Generando Reporte....
    <reporte-registro
      v-if="show"
      :item_selected="item_selected"
    />
  </div>
</template>

<script>
import ReporteRegistro from "../postulante/components/viewcomponents/reportes/ReporteRegistro.vue";
export default {
  components: { ReporteRegistro },

  data() {
    return {
      item_selected: {},
      show: false,
    };
  },

  methods: {
    async set_postulante() {
      await this.$store
        .dispatch("postulante/set_postulante_by_id", {
          id: this.$route.params.id,
          conFoto: false,
        })
        .then((resp) => {
          this.item_selected = resp[0];
        });
    },
  },

  async mounted() {
    await this.$store.dispatch("auth/set_user");
    await this.set_postulante();
    this.show = true;
  },
};
</script>

<style lang="scss" scoped></style>
